// loginReducer.js

const initialState = {
  timeOff: [],
  teamTimeOff: [],
  data: [],
  userid:[],
  admintimeOff:[],

  employees: [],
  attendance: [],
  adminattendance:[],
  teamattendance:[],
  calculation:[],
  isClockedIn: false,

  profile: {
    profile: [],
    headerprofile:[],
    adminheaderprofie:[],
    employeeInformtaion: [],
    adminemployeeinformation:{},
    adminprobation:{},
    probation: [],
    workSchedule: [],
    jobTimeLine: [],
    sideProfile: [],
    adminworkschedule :[],

    currentPage: 1,
  },

  createemploye: [],
  deleteemploye: [],

  Employeeprofile: null,
  loading: false,
  error: null,
  Sideemployeprofile: null,
};

console.log(initialState);
const loginReducer = (state = initialState, action) => {
  switch (action.type) {
    case "LOGIN_SUCCESS":
      return {
        ...state,
        isLoggedIn: true,
        loginError: null,
      };
    case "LOGIN_FAILURE":
      return {
        ...state,
        isLoggedIn: false,
        loginError: true,
      };
    case "SET_EMPLOYEES":
      return {
        ...state,
        employees: action.payload,
      };
    case "SET_ATTENDANCE":
      return {
        ...state,
        attendance: action.payload,
      };
      case "SET_ADMIN_ATTENDANCE":
        return {
          ...state,
          adminattendance: action.payload,
        };
    case "CLOCK_IN":
      return {
        ...state,
        isClockedIn: true,
      };
    case "CLOCK_OUT":
      return {
        ...state,
        isClockedIn: false,
      };
      case "ADMIN_CLOCK_IN":
        return {
          ...state,
          isClockedIn: true,
        };
      case "ADMIN_CLOCK_OUT":
        return {
          ...state,
          isClockedIn: false,
        };
    case "SET_CALCULATION":
      return {
        ...state,
        calculation: action.payload,
      };
      case "SET_ADMIN_CALCULATION":
      return {
        ...state,
        calculation: action.payload,
      };
      case "SET_TEAM_ATTENDANCE":
        return {
          ...state,
          teamattendance: action.payload,
        };
        case "SET_HEADERPROFILE":
        return {
          ...state,
          headerprofile: action.payload,
        };
         case "SET_ADMIN_HEADERPROFILE":
        return {
          ...state,
          adminheaderprofie: action.payload,
        };
    default:
      return state;
  }
};

const timeOffReducer = (state = initialState, action) => {
  console.log(action.type);
  switch (action.type) {
    case "SET_TIME_OFF":
      return {
        ...state,
        timeOff: action.payload,
      };
    case "SET_TEAMTIMEOFF":
      return {
        ...state,
        teamtimeoff: action.payload,
      };
    case "CREATE_TIMESOFF_SUCCESS":
      return {
        ...state,
        data: action.payload,
      };
      case "CREATE_ADMIN_TIMESOFF_SUCCESS":
        return {
          ...state,
          userid: action.payload,
        };
      case "SET_ADMIN_TIME_OFF":
        return {
          ...state,
          admintimeOff: action.payload,
        };
    default:
      return state;
  }
};
const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case "SET_PROFILE":
      return {
        ...state,
        profile: action.payload,
      };
    case "SET_EMPLOYMENT_INFORMATION":
      return {
        ...state,
        employeeinformtaion: action.payload,
      };
    case "SET_PROBATION":
      return {
        ...state,
        probation: action.payload,
      };
    case "SET_WORKSCHEDULE":
      return {
        ...state,
        workschedule: action.payload,
      };
    case "SET_JOBTIMELINE":
      return {
        ...state,
        jobtimeline: action.payload,
      };
    case "SET_SIDEPROFILE":
      return {
        ...state,
        sideprofile: action.payload,
      };
      case "SET_ADMIN_EMPLOYMENT_INFORMATION":
        return{
          ...state,
          adminemployeeinformation: action.payload,
        }
        case "SET_ADMIN_PROBATION":
          return{
            ...state,
            adminprobation: action.payload,
          }
          case "SET_ADMIN_WORKSCHEDULE":
            return {
              ...state,
              adminworkschedule: action.payload,
            };
            case "SET_ADMIN_JOBTIMELINE":
              return {
                ...state,
                adminjobtimeline: action.payload,
              };
    default:
      return state;
  }
};
const paginationReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHANGE_CURRENT_PAGE":
      return { ...state, currentPage: action.payload };
    // ... other cases
    default:
      return state;
  }
};
const createemployee = (state = initialState, action) => {
  switch (action.type) {
    case "CREATE_EMPLOYEE":
      return { ...state, createemploye: action.payload };
    case "DELETE_EMPLOYEE":
      // Filter out the deleted employee from the state based on its ID
      return {
        ...state,
        createemploye: state.createemploye.filter(
          (employee) => employee.employeeId !== action.payload
        ),
      };
    default:
      return state;
  }
};

const EmployeeprofileReducer = (state = initialState, action) => {
  switch (action.type) {
    case "FETCH_PROFILE_REQUEST":
      return {
        ...state,
        loading: true,
        error: null,
      };
    case "FETCH_PROFILE_SUCCESS":
      return {
        ...state,
        loading: false,
        Employeeprofile: action.payload,
      };
    case "FETCH_PROFILE_FAILURE":
      return {
        ...state,
        loading: false,
        error: action.payload,
      };
    case "FETCH_SIDEPROFILE_REQUEST": {
      return {
        ...state,
        loading: true,
        error: null,
      };
    }
    case "FETCH_SIDEPROFILE_SUCCESS":
      return {
        ...state,
        loading: false,
        Sideemployeprofile: action.payload,
      };
    case "UPDATE_PROFILE_REQUEST":
      return {
        ...state,
        loading: true,
        error: null,
      };
    case "UPDATE_PROFILE_SUCCESS":
      return {
        ...state,
        loading: false,
      };
      case 'ADD_ADDRESS_REQUEST':
      return {
         ...state,
        loading: true,
        error: null,
      };
    case 'ADD_ADDRESS_SUCCESS':
      return {
        ...state,
        loading: false,
      };
      case 'ADD_EMPLOYEINFO_REQUEST':
        return {
           ...state,
          loading: true,
          error: null,
        };
      case 'ADD_EMPLOYEINFO_SUCCESS':
        return {
          ...state,
          loading: false,
        };
      case 'ADD_EMERGENCY_REQUEST':
        return {
           ...state,
          loading: true,
          error: null,
        };
      case 'ADD_EMERGENCY_SUCCESS':
        return {
          ...state,
          loading: false,
        };
    default:
      return state;
  }
};
export {
  loginReducer,
  timeOffReducer,
  profileReducer,
  paginationReducer,
  createemployee,
  EmployeeprofileReducer,
};
