import { ATTENDANCESESSION, BASE_URL, EMPLOYEE, USER_LOGIN, GETBYEMPLOYEEID, GETBYUSERID , GETEMPLOYEEATTENDANCESYMMARY} from "./apiconstant";
import Api from ".";

export function employee() {
  var url = BASE_URL + EMPLOYEE;
  console.log(url)
  var headers = {
    'Content-Type': 'application/x-www-form-urlencoded'
  };
  return Api(url, 'GET', headers);

}
export function login(email, password) {
  var url = BASE_URL + USER_LOGIN;
  console.log(url)
  var data = {
    email: email,
    password: password
  }
  console.log(data)
  var headers = {
    'Content-Type': 'application/json'
  };
  return Api(url, 'POST', data, headers);
}

export function attendance(data) {
  var url = `${BASE_URL}${ATTENDANCESESSION}${GETBYEMPLOYEEID}${data}`;
  console.log(url)


  var headers = {
    'Content-Type': 'application/json'
  };
  return Api(url, 'GET', null, headers);

}
export function adminattendance(data) {
  var url = `${BASE_URL}${ATTENDANCESESSION}${GETBYUSERID}${data}`;
  console.log(url)


  var headers = {
    'Content-Type': 'application/json'
  };
  return Api(url, 'GET', null, headers);

}
export function teamattendance() {
  var url = BASE_URL + ATTENDANCESESSION + GETEMPLOYEEATTENDANCESYMMARY;
  console.log(url)


  var headers = {
    'Content-Type': 'application/json'
  };
  return Api(url, 'GET', null, headers);

}