function* clockInSaga(action) {
  try {
    console.log("action" ,action)
    const { latitude, longitude } = action.payload;

    // Call your clock in API here
    // For example, using 'yield call(axios.post, url, data)' to make a POST request
    const response = yield call(
      axios.post,
      `https://10.0.0.168/hrmscloneproject/api/ClockApi/clockin/${data}?latitude=${latitude}&longitude=${longitude}`
    );
    console.log("clock in api intiated", response);
    // Handle the response as needed
    // For example, if your API returns some data, you can dispatch an action with the received data
    // Or just dispatch the clockIn action to update the state
    yield put(clockIn());
  } catch (error) {
    // Handle error if needed
  }
}
