// Function to make the API call
const updateProfileAPI = (employeeId, updatedProfile) => {
  const url = `https://10.0.0.168/hrmscloneproject/api/Profile/${employeeId}`;
  return axios.post(url, updatedProfile);
};

// Saga function to handle the API call
function* updateProfileSaga(action) {
  try {
    yield call(updateProfileAPI, action.payload.employeeId, action.payload.updatedProfile);
    yield put(updateProfileSuccess());
  } catch (error) {
    
  }
}
