// Root saga to combine all watcher sagas
export default function* rootSaga() {
  yield all([
    watchLoginSaga(),
    watchFetchEmployeesSaga(),
    watchFetchAttendanceSaga(),
    watchFetchTimeOffSaga(),
    watchFetchProfileSaga(),
    watchClockInSaga(),
    watchClockOutSaga(),
    watchEmployeeInformation(),
    watchprobation(),
    watchworkschedule(),
    watchjobtimeline(),
    watchsideprofile(),
    watchFetchTeamtimeoffSaga(),
    watchCreateTimesoffSaga(),
    watchCreateEmployeSaga(),
    watchDeleteEmployeSaga(),
    watchEmployePrfile(),
    watchEmployeeSideProfile(),
    watchUpdateProfile(),
    watchAddAddress(),
    watchAddEmergency(),
    watchAddBank(),
    watchAttendanceCalculation(),
    watchTeamAttendace(),
    watchAdminEmpInfo(),
    watchAdminProbation(),
    watchAdminworkschedule(),
    watchAdminJobtime(),
    watchUpdateEmploeInfo(),
    watchAdminFetchTimeOffSaga(),
    watchAdminFetchAttendanceSaga(),
    watchAdminCreateTimesoffSaga(),
    watchAdminClockInSaga(),
    watchAdminClockOutSaga(),
    watchAdminAttendanceCalculation(),
    watchHeaderPrfile(),
    watchAdminHeaderPrfile()
    // Add more watcher sagas here if needed
  ]);
}
