export const login = (credentials) => {
  console.log(credentials);
  return {
    type: "LOGIN",
    payload: credentials,
  };
};

export const loginSuccess = () => {
  return {
    type: "LOGIN_SUCCESS",
  };
};

export const loginFailure = () => {
  return {
    type: "LOGIN_FAILURE",
  };
};

