import { lazy } from "react";
import { Navigate } from "react-router-dom";
import ProtectedRoute from "./ProtectedRoute.js";


 const role = localStorage.getItem("role")
/****Layouts*****/
const FullLayout = lazy(() => import("../layouts/FullLayout.js"));

/***** Pages ****/
const Login = lazy(()=>import("../views/Login.js"))
const Starter = lazy(() => import("../views/Starter.js"));
const About = lazy(() => import("../views/About.js"));
const Alerts = lazy(() => import("../views/Alerts"));
const Profile = lazy(() => import("../views/Profile.js"));
const Timeoff = lazy(() => import("../views/Timeoff.js"));
const Employee = lazy(() => import("../views/Employee.js"));
const Grid = lazy(() => import("../views/Grid"));
const Job = lazy(() => import("../views/Job.js"));
const TeamTimeoff = lazy(() => import("../views/TeamTimeoff.js"));
const Attendance = lazy(() => import("../views/Attendance.js"));
const TeamAttendance = lazy(()=> import("../views/TeamAttendance.js"))


// Admin page //
const Directory = lazy(() => import("../Admin/Directory.js"))
const ManageEmploye = lazy(()=> import("../Admin/ManageEmployee.js"))
const AdminTimeoff =  lazy(()=>import("../Admin/Admintimeoff.js"))
const AdminTeamoff = lazy(()=> import("../Admin/Adminteamoff.js"))
const Employeetimeoff = lazy(()=> import("../Admin/Employetimeoff.js"))
const EmployeePorfile = lazy(()=> import("../Admin/Employeeprofile.js"))
const AdminAttendance = lazy(()=> import("../Admin/Adminattendance.js"))
const AdminTeamAttendance = lazy(()=> import("../Admin/Adminteamattendance.js"))
const AdminEmpAttendance = lazy(()=> import("../Admin/AdminEmpAttd.js"))
const EmployeeJob =  lazy(()=> import("../Admin/Employeejob.js"))
const QRCode = lazy(()=>import("../Admin/Qrcode.js"))
/*****Routes******/

const ThemeRoutes = [

  { path: "/", exact: true, element: <Login /> },
  {
    path: "/",
    element: <FullLayout />,
    children: [
      // { path: "/", element: <Navigate to="/starter" /> },
      // { path: "/starter", exact: true, element: <Starter /> },
      { path: "/about", exact: true, element: <About /> },
      { path: "/alerts", exact: true, element: <Alerts /> },
      { path: "/profile", exact: true, element: <Profile /> },
      { path: "/timeoff", exact: true, element: <Timeoff /> },
      { path: "/employees", exact: true, element: <Employee /> },
      { path: "/grid", exact: true, element: <Grid /> },
      { path: "/job", exact: true, element: <Job /> },
      { path: "/teamtimeoff", exact: true, element: <TeamTimeoff /> },
      { path: "/attendance", exact: true, element: <Attendance /> },
      { path:"/teamattendance", exact: true , element:<TeamAttendance/>},
      // Admin routes
      { path: "/directory", exact: true, element: <Directory/> },
      {path:"/manageemployee", exact: true  , element: <ManageEmploye/>},
      {path:"/admintimeoff", exact: true  , element: <AdminTimeoff/>},
      {path:"/adminteamoff", exact: true  , element: <AdminTeamoff/>},
      {path:"/employeetimeoff" , exact: true , element : <Employeetimeoff/>},
      {path:"/employeeprofile" , exact: true , element : <EmployeePorfile/>},
      {path:"/adminattendance" , exact: true , element : <AdminAttendance/>},
      {path:"/adminteamattendance" , exact: true , element:<AdminTeamAttendance/>},
      {path:"/adminempattendance" , exact: true , element:<AdminEmpAttendance/>},
      {path:"/employejob" ,  exact: true , element:<EmployeeJob/>},
      {path:"/qrcode",  exact: true, element:<QRCode/>},
     
    ],
  },
];

export default ThemeRoutes;
